<template>
  <div class="content-navigator-selection">
    <uc-list-selectable
      v-model="selection"
      item-key="guid"
      :items="items"
    >
      <template #default="{ item }">
        <content-navigator-item :item="item" />
      </template>
      <template
        v-if="$scopedSlots.actions"
        #actions
      >
        <slot name="actions" />
      </template>
    </uc-list-selectable>
  </div>
</template>

<script>
import UcListSelectable from '@/shared/components/generic/uc-list-selectable'
import ContentNavigatorItem from '../content-navigator-item'

export default {
  components: {
    UcListSelectable,
    ContentNavigatorItem
  },
  props: {
    items: {
      type: Array,
      required: true
    },
    selection: {
      type: Array,
      default: () => []
    }
  }
}
</script>
<style lang="stylus" scoped>
.content-navigator-selection
  display flex
  flex 1 1 auto
  flex-direction column
  height 100%
</style>
