<template>
  <div class="uc-navigator">
    <!-- Loading state -->
    <div
      v-show="isLoading"
      class="uc-navigator__pending"
    >
      <loading-indicator :is-loading="isLoading" />
    </div>
    <template v-show="!isLoading">
      <!-- Empty state -->
      <slot
        v-if="isEmptyRootState"
        name="emptyContent"
      >
        <uc-navigator-empty-state
          :title="noItemsTitle"
          :text="noItemsText"
        />
      </slot>

      <!-- Content -->
      <template v-if="!isEmptyRootState">
        <div class="uc-navigator__header">
          <div class="uc-navigator__header--inline">
            <uc-breadcrumbs
              :items="navigation"
              @click="navigate"
            />
            <slot name="actions" />
          </div>
        </div>
        <slot
          v-if="nodes.length === 0"
          name="emptyFolder"
        />
        <div
          v-else
          class="uc-navigator__content"
        >
          <slot name="content" />
        </div>
      </template>
    </template>
  </div>
</template>

<script>
import LoadingIndicator from '@/shared/components/widgets/loading-indicator'
import UcBreadcrumbs from '@/shared/components/widgets/uc-breadcrumbs/uc-breadcrumbs'
import UcNavigatorEmptyState from './components/uc-navigator-empty-state'

export default {
  name: 'UcNavigator',
  components: {
    UcBreadcrumbs,
    LoadingIndicator,
    UcNavigatorEmptyState
  },
  props: {
    nodes: {
      type: Array,
      required: true
    },
    isLoading: {
      type: Boolean,
      default: false
    },
    navigation: {
      type: Array,
      default: () => []
    },
    isEmptyRootState: {
      type: Boolean,
      default: false
    },
    noItemsTitle: {
      type: String,
      default: ''
    },
    noItemsText: {
      type: String,
      default: ''
    }

  },
  methods: {
    navigate (breadcrumb) {
      this.$emit('navigate', breadcrumb)
    }
  }
}

</script>

<style scoped lang="stylus">
.uc-navigator
  display flex
  flex-direction column
  height 100%

.uc-navigator__pending
  position absolute
  top 0
  right 0
  bottom 0
  left 0
  z-index 100
  display flex
  align-items center
  justify-content center
  background-color rgba(255, 255, 255, 1)

.uc-navigator__header
  height 48px

.uc-navigator__header--inline
  display flex
  align-items center
  justify-content space-between

.uc-navigator__content
  flex 1 0 auto
  height 0
</style>
