<template>
  <button
    class="uc-button"
    :class="{ primary, floating, big, disabled, outlined, ...cssClassObject }"
    :disabled="disabled"
    :type="type"
    data-test="uc-button"
    @click="clicked"
  >
    <div
      v-if="hasIconBefore || centralSpacing"
      class="before-icon-wrapper"
    >
      <slot name="before" />
    </div>
    <span
      class="btn-text"
      data-test="btn-text"
      :class="{ centered: centralSpacing }"
    >
      <slot>
        {{ text }}
      </slot>
    </span>
    <div
      v-if="hasIconAfter || centralSpacing"
      class="after-icon-wrapper"
    >
      <slot name="after" />
    </div>
  </button>
</template>

<script>
import { validateAgainstEnum } from '@/util/validateAgainst'

export const themeProp = {
  PRIMARY: 'primary',
  SECONDARY: 'secondary',
  WHITE: 'white',
  GHOST: 'ghost',
  TEXT: 'text'
}

export default {
  name: 'UcButton',
  props: {
    theme: {
      type: String,
      default: themeProp.PRIMARY,
      validator: validateAgainstEnum(themeProp)
    },
    text: {
      type: String,
      default: ''
    },
    type: {
      type: String,
      default: 'button'
    },
    primary: {
      type: Boolean,
      default: false
    },
    floating: {
      type: Boolean,
      default: false
    },
    big: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    outlined: {
      type: Boolean,
      default: false
    },
    centralSpacing: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    cssClassObject () {
      return {
        'uc-button--theme-primary': this.theme === themeProp.PRIMARY,
        'uc-button--theme-secondary': this.theme === themeProp.SECONDARY,
        'uc-button--theme-ghost': this.theme === themeProp.GHOST,
        'uc-button--theme-white': this.theme === themeProp.WHITE,
        'uc-button--theme-text': this.theme === themeProp.TEXT,
        'has-icon has-icon-before': this.hasIconBefore,
        'has-icon has-icon-after': this.hasIconAfter
      }
    },
    hasIconBefore () {
      return !!this.$slots.before
    },
    hasIconAfter () {
      return !!this.$slots.after
    }
  },
  methods: {
    clicked (e) {
      this.$emit('click', e)
    }
  }
}
</script>

<style lang="stylus" scoped>
.btn-text:only-child
  padding 0px 3 * $base-unit

.uc-button
  primary-details-text()
  display flex
  align-items center
  justify-content space-evenly
  max-width 200px
  height 4 * $base-unit
  padding 0 3px
  border none
  border-radius 2 * $base-unit
  background-color $white
  outline none
  cursor pointer
  transition background-color 150ms ease
  user-select none
  pointer-events auto

  &:hover
    text-decoration underline

  &.big
    height 6 * $base-unit
    border-radius 3 * $base-unit
    primary-body-text()

  &.primary
    color $white
    font-weight $base-font-weight-semibold
    background-color $ucommerce-blue

    &:active:not(.disabled)
      box-shadow none
      transform scale(0.95)

    &:hover:not(.disabled)
      text-decoration none
      background-color lighten($ucommerce-blue, 8)

  &.floating
    box-shadow $base-box-shadow

  &.disabled
    text-decoration none
    background-color $input-field-stroke
    cursor default

  &.has-icon
    display flex
    align-items center

    &.has-icon-before
      .btn-text
        padding 0 3 * $base-unit 0 $base-unit

    &.has-icon-after
      .btn-text
        padding 0 2 * $base-unit 0 3 * $base-unit

  &.outlined
    border solid 1px $input-field-stroke
    primary-details-text-semibold()
    border-radius 2 * $base-unit

.after-icon-wrapper,
.before-icon-wrapper
  display inline-flex

.centered
  padding 0 2 * $base-unit

.uc-button--theme-text
  height auto
  padding 0
  border-radius 0
  background-color transparent
</style>
