<template>
  <div class="uc-image-picker-selection">
    <uc-list-selectable
      v-model="selection"
      item-key="guid"
      :items="items"
    >
      <template #default="{ item }">
        <div class="uc-image-picker-selection__item">
          <div class="uc-image-picker-selection__item__image">
            <uc-image
              v-if="isImage(item.nodeType)"
              :src="item.url"
              :alt="item.name"
              fit="scaleDown"
            />
            <file-icon v-else-if="isFile(item.nodeType)" />
            <folder-icon v-else />
          </div>
          <div class="uc-image-picker-selection__item__meta">
            <div class="uc-image-picker-selection__item__meta__name">
              {{ item.name }}
            </div>
            <div
              v-if="item.url"
              class="uc-image-picker-selection__item__meta__url"
            >
              {{ item.url }}
            </div>
          </div>
        </div>
      </template>
    </uc-list-selectable>
  </div>
</template>

<script>
import UcListSelectable from '@/shared/components/generic/uc-list-selectable'
import UcImage from '@/shared/components/generic/uc-image'
import FolderIcon from '@/shared/assets/illustrations/folder-image'
import FileIcon from '@/shared/assets/illustrations/file-image'
import constants from '@/shared/js/constants'

export default {
  name: 'UcImagePickerSelection',
  components: {
    UcListSelectable,
    UcImage,
    FolderIcon,
    FileIcon
  },
  props: {
    items: {
      type: Array,
      required: true
    },
    selection: {
      type: Array,
      required: true
    }
  },
  methods: {
    isFile (nodeType) {
      return nodeType.toLowerCase() === constants.NodeType.File
    },
    isImage (nodeType) {
      return nodeType.toLowerCase() === constants.NodeType.Image
    }
  }
}
</script>
<style lang="stylus" scoped>
.uc-image-picker-selection
  height 100%

.uc-image-picker-selection__item
  display flex
  align-items center

.uc-image-picker-selection__item__image
  display flex
  flex 1 0 auto
  align-items center
  justify-content center
  width $base-unit * 10
  height $base-unit * 8
  margin-right $base-unit * 3
  border $base-border
  border-radius $base-border-radius
  overflow hidden

.uc-image-picker-selection__item__meta
  padding-right $base-unit * 3

.uc-image-picker-selection__item__meta__name
  height $base-unit * 3

.uc-image-picker-selection__item__meta__url
  secondary-petit-text()

</style>
