<template>
  <div
    class="uc-navigator-grid-node"
    :class="cssClassObject"
    @click="$emit('select')"
  >
    <div class="uc-navigator-grid-node__container">
      <div
        v-truncate
        class="uc-navigator-grid-node__left-area"
      >
        <slot />
        <div
          v-truncate
          class="uc-navigator-grid-node__name"
        >
          {{ node.name }}
        </div>
      </div>
      <div
        v-if="hasChildren"
        class="uc-navigator-grid-node__right-area"
      >
        <div class="uc-navigator-grid-node__children-count">
          {{ $tc('global.items', node.childrenCount, { count: node.childrenCount }) }}
        </div>
        <div class="uc-navigator-grid-node__button">
          <uc-button-round
            theme="ghost"
            size="s"
            @click.stop="$emit('open', node)"
          >
            <chevron-forward-icon />
          </uc-button-round>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ChevronForward16Thick as ChevronForwardIcon } from '@/shared/icons'
import UcButtonRound from '@/shared/components/forms/uc-button-round'

export default {
  name: 'UcNavigatorGridNode',
  components: {
    UcButtonRound,
    ChevronForwardIcon
  },
  props: {
    node: {
      type: Object,
      required: true
    },
    selected: {
      type: Boolean,
      default: false
    },
    hasChildren: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    cssClassObject () {
      return {
        'is-selected': this.selected
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.uc-navigator-grid-node
  position relative
  width 100%
  border $base-border
  border-radius ($base-unit / 2)
  background-color $base-color-white
  cursor pointer

  &:hover
    border-color $ucommerce-blue

  &.is-selected
    border-color transparent

    // Prevent boxes from jumping, when getting wider border on selected
    &::after
      position absolute
      top 0
      left 0
      border 2px solid $ucommerce-blue
      border-radius ($base-unit / 2)
      content ''
      pointer-events none
      box(calc( 100% - 4px))

.uc-navigator-grid-node__container
  display flex
  justify-content space-between
  height 6 * $base-unit
  padding $base-unit 2 * $base-unit

.uc-navigator-grid-node__left-area
  display flex
  align-items center

.uc-navigator-grid-node__name
  primary-petit-text()
  margin 0 $base-unit
  color $primary-text

.uc-navigator-grid-node__right-area
  display flex
  align-items center
  justify-content flex-end

.uc-navigator-grid-node__children-count
  primary-petit-text()
  margin-right 3 * $base-unit
  color $secondary-text
  line-height inherit
  white-space nowrap

.uc-navigator-grid-node__button
  box(3 * $base-unit)
</style>
