<template>
  <div class="content-navigator-grid-node">
    <uc-navigator-grid-node
      :node="node"
      data-test="grid-node"
      :has-children="hasChildren"
      :selected="selected"
      @select="$emit('select', node)"
      @open="$emit('open', node)"
    >
      <span class="content-navigator-grid-node__icon">
        <content-branch-icon v-if="hasChildren" />
        <content-leaf-icon v-else />
      </span>
    </uc-navigator-grid-node>
  </div>
</template>

<script>
import UcNavigatorGridNode from '@/shared/components/generic/uc-navigator/components/nodes/uc-navigator-grid-node'
import { ContentBranch24Thick as ContentBranchIcon, ContentLeaf24Thick as ContentLeafIcon } from '@/shared/icons'

export default {
  name: 'ContentNavigatorGridBranch',
  components: {
    UcNavigatorGridNode,
    ContentBranchIcon,
    ContentLeafIcon
  },
  props: {
    node: {
      type: Object,
      required: true
    },
    selected: {
      type: Boolean,
      default: false
    },
    hasChildren: {
      type: Boolean,
      default: false
    }
  }
}
</script>

<style lang="stylus" scoped>
.content-navigator-grid-node__icon
  box(3 * $base-unit)
</style>
