<template>
  <section
    class="fields-section"
    :class="cssClassObject"
    :v-custom-scroll-bar="scrollable"
    @click="$emit('click')"
  >
    <div class="fields-section__actions">
      <slot name="actions" />
    </div>
    <h2
      v-if="heading"
      class="fields-section__heading"
    >
      {{ heading }}
    </h2>
    <slot />
  </section>
</template>

<script>
export default {
  props: {
    heading: {
      type: String,
      default: ''
    },
    scrollable: {
      type: Boolean,
      default: false
    },
    paddingless: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    cssClassObject () {
      return {
        'fields-section--scrollable': this.scrollable,
        'fields-section--paddingless': this.paddingless
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.fields-section
  margin 0 auto
  padding 4 * $base-unit 3 * $base-unit
  background-color $ucommerce-white
  box-shadow 0 2px 8px 0 rgba(0, 0, 0, 0.1)

.fields-section + .fields-section
  margin-top 2.5 * $base-unit

.fields-section__heading
  margin 0 0 3 * $base-unit
  primary-heading-2()
  user-select none

.fields-section__actions
  display flex
  justify-content flex-end

.fields-section--scrollable
  max-height 575px
  overflow-y auto

.fields-section--paddingless
  padding 0
</style>
