<template>
  <div class="uc-image-picker-grid-file">
    <uc-navigator-grid-leaf-node
      data-test="uc-image-picker-grid-file"
      :selected="selected"
      :node="node"
      @select="select"
    >
      <file-icon />
    </uc-navigator-grid-leaf-node>
  </div>
</template>

<script>
import UcNavigatorGridLeafNode from '@/shared/components/generic/uc-navigator/components/nodes/uc-navigator-grid-leaf-node'
import FileIcon from '@/shared/assets/illustrations/file-image'

const ComponentEvent = {
  SELECT: 'select'
}

export default {
  name: 'UcImagePickerGridFile',
  components: {
    UcNavigatorGridLeafNode,
    FileIcon
  },
  props: {
    node: {
      type: Object,
      required: true
    },
    selected: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    select () {
      this.$emit(ComponentEvent.SELECT, this.node)
    }
  }
}
</script>
